import styled from '@emotion/styled';
import { StaticImage } from 'gatsby-plugin-image';

export default function BackgroundImage() {
  return (
    <HeaderBackground>
      <StaticImage
        src="../../content/img/common/back5.jpg"
        alt="header background"
        placeholder="blurred"
        layout="fullWidth"
      />
    </HeaderBackground>
  );
}

const HeaderBackground = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 350px;

  img {
    width: 100%;
    height: 350px;
  }

  @media (max-width: 780px) {
    height: 28rem;
  }

  @media (max-width: 500px) {
    height: 25rem;
  }
`;
